body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  background: radial-gradient(#a4d6f2, #0998d3);
}

#root {
  position: relative;
  width: 100%;
  max-width: 640px;
  height: 100%;
  max-height: 640px;
  background-color: rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}

video,
canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 640px;
  height: 640px;
  transform: translate(-50%, -50%) scale(-1, 1);
}

button {
  position: absolute;
  top: 25px;
  right: 25px;
  width: 50px;
  height: 50px;
  border: 0;
  background-color: #0998d3;
  box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.25);
  font-size: 20px;
  line-height: 1;
  color: #fff;
}
